<script src="../../../../../yijjixhaxun/vue/src/router/index.js"></script>
<template>
    <div id="stayls">
        <headerclass></headerclass>
        <pu-blicto></pu-blicto>
        <div class="Whole">
            <div class="claope">
                <p class="fello">客户概览</p>
                <button class="Establish" @click="Establish">创建</button>
                <div class="">
                    <el-table
                            :data="tableData"
                            style="width: 100%">
                        <el-table-column
                                label="序号"
                                width="70"
                                type="index">
                        </el-table-column>
                        <el-table-column
                                prop="custName"
                                label="客户名称"

                        >
                        </el-table-column>
                        <el-table-column
                                prop="custNumber"
                                label="客户编号">
                        </el-table-column>
                        <el-table-column
                                prop="time"
                                label="公钥">
                            <template slot-scope="scope">
                                <el-button
                                        @click="handleEdit(scope.$index, scope.row)">查看
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="number"
                                label="计费">
                            <template slot-scope="scope">
                                <el-button
                                        @click="handleDelete(scope.$index, scope.row)">查看
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="amount"
                                label="余额">
                            <template slot-scope="scope">
                                <el-button @click="balanceDelete(scope.$index, scope.row)">
                                    {{scope.row.amount}}
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="downwell"
                                label="应用场景">
                            <template slot-scope="scope">
                                <el-button @click="distribution(scope.$index, scope.row)">分配</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="isPause"
                                label="暂停/启用">
                            <template slot-scope="scope">
                                <el-button @click="EnableDelete(scope.$index, scope.row)">
                                    <span>{{scope.row.isPause?'启用':'暂停'}}</span>
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="download"
                                label="操作">
                            <template slot-scope="scope">
                                <el-button @click="deleteDelete(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="paging">
                    <div class="block_plo">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :prev-text="'上一页'"
                                :next-text="'下一页'"
                                :page-count="pageCount"
                                :page-size="pagesize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pageTotal">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <!--创建客户-->
        <div class="Modal">
            <div class="modelto Create" v-if="logop">
                <div class='list'>
                    <div class="Adtion_t">
                        <p>创建客户</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="pacc-le">
                            <label for="">客户名称:</label>
                            <el-input
                                    placeholder="请输入客户名称"
                                    v-model="input"
                                    clearable>
                            </el-input>
                        </div>
                        <div class="pacc-le">
                            <label for="">接口密钥:</label>
                            <el-input
                                    placeholder="请输入接口密钥"
                                    v-model="Interface"
                                    clearable>
                            </el-input>
                        </div>
                        <div class="form-group plert" style="margin-top: 25px;">
                            <button class="calopi" @click="Eliemker">创建</button>
                            <button class="Cancel" @click="Cancel_cli">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--删除 -->
        <div class="Modal">
            <div class="modelto delect" v-if="delect">
                <div class='list_de'>
                    <div class="delop">
                        <p>提示</p>
                        <img src="~@assets/img/sop.png" alt="" @click="delectmoer">
                    </div>
                    <div class="deler">
                        <p>是否要删除该信息</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="form-group btuo" style="margin-top: 25px;">
                            <button class="calopi" @click="deleng">确定</button>
                            <button class="Cancel" @click="delecting">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--密钥-->
        <div class="Modal">
            <div class="modelto secret" v-if="secret">
                <div class='list-wll'>
                    <div class="Adtiko">
                        <p>密钥</p>
                        <div class="demo-input-suffix">
                            客户名称：
                            <el-input
                                    v-model="Keyinformation"
                                    :readOnly="true">
                            </el-input>
                        </div>
                    </div>
                    <div class="sronly-xs">
                        <div class="tece_le">
                            <label for  ="">公钥:</label>
                            <span class="kioser" v-if="file">{{file.name}}</span>
                            <a href="javascript:;" class="fellop">
                                <span class="ketle">上传</span>
                                <input type="file" name="contractFileName"  ref="info" @change="changeImage($event)" class="flieing" id="file" />
                            </a>
                        </div>
                        <div class="form-group plert" style="margin-top: 30px;">
                            <button class="calopi" @click="Submission">提交</button>
                            <button class="Cancel" @click="calkoe">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--余额-->
        <div class="Modal">
            <div class="modelto money_li" v-if="money_op">
                <div class='list-mode'>
                    <div class="mkei">
                        <p class="modey">余额</p>
                        <div class="demo-input-suffix">
                            客户名称：
                            <el-input
                                    v-model="information"
                                    :readOnly="true">
                            </el-input>
                        </div>
                    </div>
                    <div class="sronly">
                        <div class="nuumekl nuerlod">
                            <label for="">当前余额:</label>
                            <input type="text" v-model="money" disabled="disabled">
                        </div>
                        <div class="nuumekl">
                            <label for="">充值:</label>
                            <el-input
                                    placeholder="保存小数点后两位，最大十万级"
                                    v-model="woet"
                                    clearable>
                            </el-input>
                        </div>
                        <div class="form-group plert" style="margin-top: 25px;">
                            <button class="calopi" @click="moder">确定</button>
                            <button class="Cancel" @click="Delete_bane">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--分配-->
        <div class="Modal">
            <div class="modelto distri" v-if="distri">
                <div class='list_distri'>
                    <div class="information">
                        <p class="loe">众人信息</p>
                    </div>
                    <div class="Body_inf">
                        <p class="scene">分配场景</p>
                        <div class="chart_is">
                            <el-table
                                    ref="multipleTable"
                                    :data="tableDato"
                                    tooltip-effect="dark"
                                    style="width: 100%"
                                    @selection-change="handleSelectionChange">
                                <el-table-column
                                        type="selection"
                                        width="55">
                                </el-table-column>
                                <el-table-column
                                        prop="appId"
                                        label="序号"
                                        width="70">
                                </el-table-column>
                                <el-table-column
                                        prop="sceneName"
                                        label="场景名称"
                                >
                                </el-table-column>
                            </el-table>
                        </div>
                        <div class="form-group neerMad">
                            <button class="Determine" @click="cliker">创建</button>
                            <el-button class="cancelTo" @click="toggleSelection()">取消</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import headerclass from "@views/Charging/header/header";
    import puBlicto from "@views/Charging/Administration/puBlicto";
    import {createCustomer, customer, recharge,deleteCust,custIsPause,uploadPublicKey,queryAllApp,allocationApp} from "@/network/admin";

    export default {
        name: "stayls",
        data() {
            return {
                tableData: [],
                tableDato:[],//场景分配
                multipleSelection: [],//获取的选择场景
                selectedItem: {},
                loading: true,
                currentPage: 1,
                pagesize: 10,
                pageTotal: 0,
                pageCount: 0,
                //分页以上
                input: '',//创建输入框内容可删除
                Interface:'',//创建输入框接口密钥
                money: 0,
                woet: '',
                information: '',
                Keyinformation:'',
                logop: false,//创建
                delect: false,//删除
                secret: false,//密钥
                money_op: false,//余额
                file:'',
                charing:'',
                distri: false ,//分配
                pplList: [],
            }
        },
        created: function () {
            this.hadleGetCustomerApi();
        },
        activated:function(){
            this.hadleGetCustomerApi();
        },
        components: {
            headerclass,
            puBlicto,
        },
        methods: {
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.hadleGetCustomerApi();
                var itemsPerPage = this.pagesize;
            },
            // 控制页面的切换
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
                var currentPage = this.currentPage;
                this.hadleGetCustomerApi();
            },
            //请求列表
            hadleGetCustomerApi() {
                var data = {
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize
                };
                createCustomer(data).then(res => {
                    // console.log(res.data.infos[0].custId);
                    this.tableData = res.data.customers;
                    this.pageTotal = res.data.total;
                    this.pageCount = res.data.pages;
                })
                    .catch({});
            },
            //创建
            Establish() {
                this.logop = true;
            },
            Cancel_cli() {
                this.input = '';
                this.Interface = '';
                this.logop = false;
            },
            //创建完成
            Eliemker() {
                var data = {
                    signData: this.Interface,
                    custName: this.input
                }
                customer(data).then(res => {
                    if (res.code== 0) {
                        layer.msg("创建成功！", {
                            icon: 1,
                            time: 1500
                        });
                        this.hadleGetCustomerApi();
                        this.Interface = '';
                        this.logop = false;
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            //密钥
            handleEdit(index, row) {
                this.secret = true;
                this.Keyinformation = row.custName;
                this.selectedItem = Object.assign({}, row);
            },
            changeImage(e){
                this.file = e.target.files[0];
            },
            //关闭密钥
            calkoe(){
                this.secret = false;
                this.file = ''
            },
            //提交密钥
            Submission() {
                var formData = new FormData();
                formData.append('file', this.file);
                let custNumber = this.selectedItem.custNumber;
                formData.append('custNumber', custNumber);
                uploadPublicKey(formData).then(res => {
                    console.log(res)
                    if (res.code== 0) {
                        layer.msg(res.msg, {
                            icon: 1,
                            time: 1500
                        });
                        this.secret = false;
                        this.file = ''
                    } else if(res.code == 500){
                        layer.msg("服务器内部异常！", {
                            icon: 2,
                            time: 1500
                        });
                    }
                })

            },
            //计费跳转
            handleDelete(index, row) {
                this.charing = row.custNumber;
                this.$router.push('/Charging/Statistical?custNumber='+this.charing);
            },
            //充值金额
            balanceDelete(index, row) {
                this.money_op = true;
                this.money = row.amount;
                this.information = row.custName;
                console.log(this.information)
                this.selectedItem = Object.assign({}, row);
            },
            moder() {
                const amount = this.woet;
                const amountreg = /^(([1-9]{1}\d{0,4})|100000|(0{1}))(\.\d{1,2})?$/;
                if (!amount) {
                    layer.msg('金额不能为空！', {
                        icon: 2,
                        time: 1500
                    });
                    return false;
                }
                if (!amountreg.test(amount)) {
                    layer.msg('输入金额不正确！', {
                        icon: 2,
                        time: 1500
                    });
                    return false;
                }
                let custId = this.selectedItem.custId;
                const data = {
                    custId: custId,
                    amount: amount
                };
                recharge(data).then(res => {
                    if (res.code== 0) {
                        layer.msg("充值成功！", {
                            icon: 1,
                            time: 1500
                        });
                        this.money_op = false;
                        this.hadleGetCustomerApi();
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })

            },
            //金额关闭
            Delete_bane(){
                this.woet = '';
                this.money = '';
                this.money_op = false;
            },
            //暂停/启用
            EnableDelete(index, row) {
                this.selectedItem = Object.assign({}, row);
                let custId = this.selectedItem.custId;
                let isPause = this.selectedItem.isPause;
                if(isPause == 0){
                    const data = {
                        custId : custId,
                        isPause : 1
                    };
                    console.log(data)
                    custIsPause(data).then(res => {
                        if (res.code== 0) {
                            layer.msg("执行成功！", {
                                icon: 1,
                                time: 1500
                            });
                            this.hadleGetCustomerApi();
                        } else if(res.code !== 0){
                            layer.msg(res.msg, {
                                icon: 2,
                                time: 1500
                            });
                        }
                    })
                } else {
                    const data = {
                        custId : custId,
                        isPause : 0
                    };
                    console.log(data)
                    custIsPause(data).then(res => {
                        if (res.code== 0) {
                            layer.msg("执行成功！", {
                                icon: 1,
                                time: 1500
                            });
                            this.hadleGetCustomerApi();
                        } else if(res.code !== 0){
                            layer.msg(res.msg, {
                                icon: 2,
                                time: 1500
                            });
                        }
                    })
                }

            },
            //删除
            deleteDelete(index, row) {
                this.delect = true;
                // let custId = row.custNumber;
                this.selectedItem = Object.assign({}, row);
            },
            deleng(){
                console.log(this.selectedItem)
                let custId = this.selectedItem.custId;
                const data = {
                    custId : custId
                };
                console.log(data)
                deleteCust(data).then(res => {
                    console.log(res)
                    if (res.code== 0) {
                        layer.msg("删除成功！", {
                            icon: 1,
                            time: 1500
                        });
                        this.delect = false;
                        this.hadleGetCustomerApi();
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            delecting(){
                this.delect = false;
            },
            delectmoer(){
                this.delect = false;
            },
            //点击分配打开弹出框
            distribution(index, row){
                this.selectedItem = row.custId;
                this.distri = true;
                var data = {
                    custId : this.selectedItem
                }
                console.log(data)
                queryAllApp(data).then(res => {
                    this.tableDato = res.data;
                    this.tableDato.forEach((item, index) => {
                        if (item.isCheck) {
                            this.$nextTick(()=>{
                                this.$refs.multipleTable.toggleRowSelection(item, true);
                            });
                        }
                    });
                })
            },
            //获取选中的
            handleSelectionChange(val) {
                this.multipleSelection = val;
                // console.log(this.multipleSelection);
            },
            //点击分配创建
            cliker(val){
                const idList = [];
                this.multipleSelection.filter(item => {
                    idList.push(item.appId);
                });
                var arr = idList.join(',');
                if(arr==''){
                    layer.msg('请选择场景', {
                        icon: 2,
                        time: 1500
                    });
                    return
                }
                const data= {
                    custId: this.selectedItem,
                    appIds: arr
                }
                allocationApp(data).then(res => {
                    if (res.code== 0) {
                        layer.msg(res.msg,{
                            icon: 1,
                            time: 1500
                        });
                        this.$refs.multipleTable.clearSelection();
                        this.distri = false;
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })
            },
            //分配关闭
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
                this.distri = false;
            },



        }
    }
</script>

<style>
    @import "~@assets/css/user/admintisop.css";
</style>